body {
    font-family: Arial, Helvetica, sans-serif;
    font-size: 15px;
    color: #454545;
    margin: 2em auto;
    padding: 1em;
    line-height: 1.4;
    text-align: justify;
}

a {
    color: #07a;
}

h1 {
    text-align: center;
}

h1, h2 {
    margin-bottom: 1em;
}

#container {
    min-width: 20em;
    max-width: 40em;
    justify-content: center;
    margin: auto;
}